"use client"

import { useEffect, useState } from "react"
import { useRouter, useParams } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { authService, type TeamDetails } from "@/lib/auth-service"
import { requestsService, type RequestData } from "@/lib/requests-service"
import { useRequestsWS } from "@/hooks/use-requests-ws"

export default function POCTeamDetailsPage() {
  const router = useRouter()
  const params = useParams()
  const teamId = params.id as string

  const [team, setTeam] = useState<TeamDetails | null>(null)
  const [requests, setRequests] = useState<RequestData[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")

  useEffect(() => {
    const fetchTeamDetails = async () => {
      try {
        const teamData = await authService.getPOCTeamDetail(teamId)
        setTeam(teamData)
      } catch (err: any) {
        setError(err.message || "Failed to load team details")
      } finally {
        setLoading(false)
      }
    }

    if (teamId) {
      fetchTeamDetails()
    }
  }, [teamId])

  const handleWsEvent = (message: any) => {
    if (message.event === "request_created" || message.event === "request_updated") {
      if (message.request?.team_id === teamId) {
        setRequests((prev) => {
          const filtered = prev.filter((r) => r.id !== message.request?.id)
          return [message.request, ...filtered].sort(
            (a, b) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime(),
          )
        })
      }
    } else if (message.event === "state_transition") {
      setRequests((prev) => prev.map((r) => (r.id === message.request_id ? { ...r, status: message.to as any } : r)))
    }
  }

  useRequestsWS(handleWsEvent)

  useEffect(() => {
    if (!teamId) return

    const fetchRequests = async () => {
      try {
        const response = await requestsService.listRequests({
          team_id: teamId,
          ordering: "-created_at",
        })
        setRequests(response.results)
      } catch (err) {
        console.error("Error fetching requests:", err)
      }
    }

    fetchRequests()
  }, [teamId])

  const handleStatusChange = async (requestId: number, newStatus: string) => {
    try {
      const updated = await requestsService.changeRequestStatus(requestId, newStatus)
      setRequests((prev) => prev.map((r) => (r.id === updated.id ? updated : r)))
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to update status")
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <div className="text-center">
          <div className="w-12 h-12 border-4 border-gray-300 border-t-orange-600 rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600 font-medium">Loading team details...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="bg-white border-b border-gray-200 sticky top-0 z-40">
        <div className="max-w-6xl mx-auto px-4 py-4">
          <div className="flex items-center justify-between gap-4">
            <div className="flex items-center gap-4">
              <Image
                src="/images/image.png"
                alt="Smart India Hackathon"
                width={120}
                height={40}
                className="h-10 w-auto"
              />
              <Image
                src="/images/gitam-logo-20-282-29.png"
                alt="GITAM"
                width={60}
                height={40}
                className="h-10 w-auto"
              />
            </div>
            <h1 className="text-2xl font-bold" style={{ color: "#002449" }}>
              Team Details
            </h1>
            <Link href="/dashboard" className="text-gray-600 hover:text-gray-900 font-medium">
              ← Back
            </Link>
          </div>
        </div>
      </header>

      <main className="max-w-6xl mx-auto px-4 py-8">
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
            <p className="text-red-800">{error}</p>
          </div>
        )}

        {team && (
          <div className="space-y-6">
            {/* Team Information */}
            <div className="bg-white border border-gray-200 rounded-lg p-8 shadow-md">
              <h2 className="text-2xl font-bold mb-6" style={{ color: "#f75700" }}>
                Team Information
              </h2>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="pb-4 border-b border-gray-200">
                  <p className="text-xs font-medium text-gray-500 uppercase mb-2">Team Name</p>
                  <p className="text-lg font-semibold text-gray-900">{team.name}</p>
                </div>

                <div className="pb-4 border-b border-gray-200">
                  <p className="text-xs font-medium text-gray-500 uppercase mb-2">Institution</p>
                  <p className="text-lg font-semibold text-gray-900">{team.institution}</p>
                </div>

                {team.problem_statement && (
                  <div className="pb-4 border-b border-gray-200 md:col-span-2">
                    <p className="text-xs font-medium text-gray-500 uppercase mb-2">Problem Statement</p>
                    <p className="text-lg font-semibold text-gray-900">{team.problem_statement.title}</p>
                    <p className="text-sm font-medium mt-2" style={{ color: "#f75700" }}>
                      ID: {team.problem_statement.id}
                    </p>
                  </div>
                )}

                {team.faculty_mentor && (
                  <div className="pb-4 border-b border-gray-200">
                    <p className="text-xs font-medium text-gray-500 uppercase mb-2">Faculty Mentor</p>
                    <p className="text-lg font-semibold text-gray-900">{team.faculty_mentor.name}</p>
                  </div>
                )}
              </div>
            </div>

            {/* Team Members */}
            <div className="bg-white border border-gray-200 rounded-lg p-8 shadow-md">
              <h2 className="text-2xl font-bold mb-6" style={{ color: "#f75700" }}>
                Team Members ({team.members?.length || 0})
              </h2>

              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                {team.members?.map((member, idx) => (
                  <div
                    key={idx}
                    className="p-4 border border-gray-200 rounded-lg hover:border-gray-400 transition-colors"
                  >
                    <p className="font-semibold text-gray-900">{member.user.name}</p>
                    <p className="text-sm text-gray-600 mt-2 break-all">{member.user.email}</p>
                    <p className="text-xs font-bold mt-3 uppercase" style={{ color: "#f75700" }}>
                      {member.role}
                    </p>
                  </div>
                ))}
              </div>
            </div>

            <div className="bg-white border border-gray-200 rounded-lg p-8 shadow-md">
              <h2 className="text-2xl font-bold mb-6" style={{ color: "#f75700" }}>
                Requests ({requests.length})
              </h2>

              {requests.length === 0 ? (
                <div className="text-center py-12">
                  <p className="text-gray-600">No requests from this team yet</p>
                </div>
              ) : (
                <div className="space-y-4">
                  {requests.map((request) => (
                    <div
                      key={request.id}
                      className="border border-gray-200 rounded-lg p-6 hover:shadow-md transition-shadow"
                    >
                      <div className="flex items-start justify-between mb-4">
                        <div className="flex-1">
                          <div className="flex items-center gap-3 mb-2">
                            <span
                              className="px-3 py-1 text-xs font-semibold text-white rounded-full"
                              style={{
                                backgroundColor:
                                  request.category === "BOM"
                                    ? "#002449"
                                    : request.category === "ADDITIONAL"
                                      ? "#007367"
                                      : "#f75700",
                              }}
                            >
                              {request.category}
                            </span>
                            <span
                              className="px-3 py-1 text-xs font-semibold text-white rounded-full"
                              style={{
                                backgroundColor:
                                  request.status === "Submitted"
                                    ? "#f75700"
                                    : request.status === "Processing"
                                      ? "#002449"
                                      : request.status === "Issued"
                                        ? "#078e31"
                                        : "#999",
                              }}
                            >
                              {request.status}
                            </span>
                          </div>
                          <h3 className="font-semibold text-gray-900">{request.notes || "Untitled Request"}</h3>
                          <p className="text-sm text-gray-600 mt-2">
                            {new Date(request.created_at).toLocaleDateString()}
                          </p>
                        </div>

                        <Link
                          href={`/requests/${request.id}`}
                          className="text-blue-600 hover:text-blue-800 font-medium text-sm"
                        >
                          View Details →
                        </Link>
                      </div>

                      {/* Request Details Preview */}
                      {request.bom_items && request.bom_items.length > 0 && (
                        <div className="mt-4 p-3 bg-gray-50 rounded-lg text-sm">
                          <p className="font-medium text-gray-700">BOM Items: {request.bom_items.length}</p>
                        </div>
                      )}

                      {request.additional_items && request.additional_items.length > 0 && (
                        <div className="mt-4 p-3 bg-gray-50 rounded-lg text-sm">
                          <p className="font-medium text-gray-700">
                            Additional Items: {request.additional_items.length}
                          </p>
                        </div>
                      )}

                      {request.fabrication && (
                        <div className="mt-4 p-3 bg-gray-50 rounded-lg text-sm">
                          <p className="font-medium text-gray-700">
                            {request.fabrication.fab_type} - {request.fabrication.name}
                          </p>
                        </div>
                      )}

                      {/* Status Actions */}
                      {(request.status === "Submitted" || request.status === "Processing") && (
                        <div className="mt-4 flex flex-wrap gap-2">
                          {request.status === "Submitted" && (
                            <>
                              <button
                                onClick={() => handleStatusChange(request.id, "Processing")}
                                className="px-4 py-2 text-white font-medium text-sm rounded-lg transition-opacity"
                                style={{ backgroundColor: "#078e31" }}
                                onMouseEnter={(e) => (e.currentTarget.style.opacity = "0.9")}
                                onMouseLeave={(e) => (e.currentTarget.style.opacity = "1")}
                              >
                                Accept
                              </button>
                              <button
                                onClick={() => handleStatusChange(request.id, "Cannot be Processed")}
                                className="px-4 py-2 text-white font-medium text-sm rounded-lg transition-opacity"
                                style={{ backgroundColor: "#f75700" }}
                                onMouseEnter={(e) => (e.currentTarget.style.opacity = "0.9")}
                                onMouseLeave={(e) => (e.currentTarget.style.opacity = "1")}
                              >
                                Reject
                              </button>
                            </>
                          )}

                          {request.status === "Processing" && (
                            <>
                              <button
                                onClick={() => handleStatusChange(request.id, "Issued")}
                                className="px-4 py-2 text-white font-medium text-sm rounded-lg transition-opacity"
                                style={{ backgroundColor: "#078e31" }}
                                onMouseEnter={(e) => (e.currentTarget.style.opacity = "0.9")}
                                onMouseLeave={(e) => (e.currentTarget.style.opacity = "1")}
                              >
                                Mark as Issued
                              </button>
                              <button
                                onClick={() => handleStatusChange(request.id, "Cannot be Processed")}
                                className="px-4 py-2 text-white font-medium text-sm rounded-lg transition-opacity"
                                style={{ backgroundColor: "#f75700" }}
                                onMouseEnter={(e) => (e.currentTarget.style.opacity = "0.9")}
                                onMouseLeave={(e) => (e.currentTarget.style.opacity = "1")}
                              >
                                Cannot Process
                              </button>
                            </>
                          )}
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        )}
      </main>
    </div>
  )
}
